import React, { useEffect, useState } from "react";
import Header from "./components/Header";
import "jspdf-autotable";
const CV = (props) => {
    const [data, setData] = useState([]);
    const [certs, setCert] =useState([]);
    const [relevance, changeRelevance] = useState('all');
    useEffect(()=>{
        const language = props?"English":"Bulgarian";
        fetch(`https://ren-portfolio-backend.herokuapp.com/bullets/language/${language}`,
        {
            method: 'GET',
            headers:{
                'Content-Type': 'application/json',
            }
        })
        .then(response => response.json())
        .then(res => setData(res))

        fetch(`https://ren-portfolio-backend.herokuapp.com/certificates/all`,
        {
            method: 'GET',
            headers:{
                'Content-Type': 'application/json',
            }
        })
        .then(response => response.json())
        .then(res => setCert(res))
    },[props]);

    const bulletComponent = (bullet) => {
        const { idbullets, description } = bullet;
        let content = bullet.content
        if(content.includes('*')){
            content = content.split('*').join('\n ');
        }
        return <tr id={props?"eng":"bg"}  
        key={description+ idbullets}>
            <td className="description">{description}:</td>
            <td className="content">{content}</td>
            </tr>;
    }

    const sectionFilteringFunction = (data) => {
        return ((bulletName) => {
            return data.filter(bullet => {
                return bullet.section === bulletName && (bullet.relevant === relevance || relevance === 'all' || bullet.relevant === 'all')
            }).map(bulletComponent);
        })
    }

    const CVdownloadComponent = (bullet) => {
        return `<${bullet.type}><${bullet.props.children[0].type} class="description"> ${bullet.props.children[0].props.children.join('')} </${bullet.props.children[0].type}> 
        <${bullet.props.children[1].type} class="content"> ${bullet.props.children[1].props.children} </${bullet.props.children[1].type}> </${bullet.type}>`
    }

    const memoisedDataFilter = sectionFilteringFunction(data);
    const personalData = memoisedDataFilter("personalData");
    const workExperience = memoisedDataFilter("workExperience");
    const education = memoisedDataFilter("education");
    const languages = memoisedDataFilter("languages");
    const socialSkills = memoisedDataFilter("socialSkills");
    const organizationalSkills = memoisedDataFilter("organizationalSkills");
    const techSkills = memoisedDataFilter("techSkills");
    const artSkills = memoisedDataFilter("artSkills");
    const otherSkills = memoisedDataFilter("otherSkills");
    const otherInfo = memoisedDataFilter("otherInfo");
    // eslint-disable-next-line react/jsx-no-target-blank
    const certificates = certs.map(a =><tr id={props?"eng":"bg"} key={a.engName+a.idcertificates}><td className="description">{props?a.engName:a.bgName}:</td><td className="content"><a className="certificate-link" href={a.link} target="_blank">{props?"Click here to go to the certificate":",     "}</a></td></tr> )
    
    const personalDataToDoc = personalData.map(CVdownloadComponent)
    const workExperienceDoc = workExperience.map(CVdownloadComponent)
    const educationDoc= education.map(CVdownloadComponent)
    const languagesDoc= languages.map(CVdownloadComponent)
    const socialSkillsDoc= socialSkills.map(CVdownloadComponent)
    const organizationalSkillsDoc= organizationalSkills.map(CVdownloadComponent)
    const techSkillsDoc= techSkills.map(CVdownloadComponent)
    const artSkillsDoc= artSkills.map(CVdownloadComponent)
    const otherSkillsDoc= otherSkills.map(CVdownloadComponent)
    const otherInfoDoc= otherInfo.map(CVdownloadComponent)

    const changeRelevanceFilter = event => {
        event.preventDefault();
        changeRelevance(event.target.name);
        event.target.id = 'selected-relevance'
    }

    const printDiv = event=>{
        event.preventDefault();
        // var divContents = document.getElementById("example").innerHTML;
        var a = window.open('', '', 'height=500, width=500');
        a.document.write('<html>');
        a.document.write('<style>');
        a.document.write('@font-face {font-family: Alt;src: url(../src/fonts/brush-type-bg-font.ttf);}')
        a.document.write(':root{--gray:#363b44;--very-dark-blue:#081B26;--light-blue-semi:#acdef2a2;--light-blue:#ACDEF2;--light-blue-2:#3DADD9;--dark-blue:#0F3D59;--teal-blue:#1C6C8C;--teal-blue-semi:#1c6c8c9c;--turquoise: #94D2BD;--turquoise-semi:  #94d2bda8;--white: rgba(255, 255, 255, 0.726);--white-semi: rgba(255, 255, 255, 0.685);--white-sem2i:rgba(255, 255, 255, 0.377);--yellow: #E9D8A6;--yellow-semi:  #e9d8a6a9;--gradient: linear-gradient(65deg, #d367c1 10%,#dedf40 25%,#62cb5c 50%,#00bbcb 75%,#ab79d6 90%);}')
        a.document.write('table.down{  padding: 1%;top: 10%;left: 10%; position: relative; width: fit-content;display: block;background-color: var(--very-dark-blue);border-radius: 1vw;font-family: Arial}')
        a.document.write('table tr{ height: 4vh;}');
        a.document.write('td.content{min-width: 25vw; max-width: 50vw; height: fit-content; font-family: Arial;   white-space:pre-wrap; word-wrap:break-word;}')
        a.document.write('td:not(td#table-indentifier) {color: var(--white);background-color: var(--gray); font-size: 1.5vw; padding: 3%; border-color: var(--gray);border-radius: 1vw;border-style: solid;}')
        a.document.write('.description{  font-size: 1.9vw;  height: 5vh; text-align: center; overflow-wrap: break-word;}')
        
        a.document.write('#information-row{  font-size: 1.8vw;  height: 5vh; text-align: center;}')
        a.document.write('#table-indentifier{    background-color: var(--light-blue-semi);border-radius: 1vw;color: var(--white);}')
        a.document.write('background-color: var(--light-blue-semi); color: var(--white)')
        a.document.write('.zero-width{  display: none;}')
        a.document.write('</style>')
        a.document.write('<body>');
        a.document.write("<table class='down'>")
        a.document.write("<tbody>")
        a.document.write(`<tr id="information-row"><td colSpan="2" id="table-indentifier">${props?"Personal Information":" "}</td><td className="zero-width"></td></tr>`)
        a.document.write(personalDataToDoc);
        a.document.write(`<tr id="information-row"><td colSpan="2" id="table-indentifier">${props?"Professional experience":" "}</td><td className="zero-width"></td></tr>`)
        a.document.write(workExperienceDoc);
        a.document.write(`<tr id="information-row"><td colSpan="2" id="table-indentifier">${props?"Special courses, qualifications and education":" ,   "}</td><td className="zero-width"></td></tr>`)
        a.document.write(educationDoc)
        a.document.write(`<tr id="information-row"><td colSpan="2" id="table-indentifier">${props?"Languages":""}</td><td className="zero-width"></td></tr>`)
        a.document.write(languagesDoc)
        a.document.write(`<tr id="information-row"><td colSpan="2" id="table-indentifier">${props?"Personal skills and competences":"   "}</td><td className="zero-width"></td></tr>`)
        a.document.write(socialSkillsDoc)
        a.document.write(organizationalSkillsDoc)
        a.document.write(techSkillsDoc)
        a.document.write(artSkillsDoc)
        a.document.write(otherSkillsDoc)
        a.document.write(otherInfoDoc)
        a.document.write("</tbody>")
        a.document.write("</table>")
        a.document.write('</body></html>');
        a.document.close();
        a.print()
    }
   return<div className="screen" >
            <Header bool={props} parent="about"/>
            <a className="back-button" href="/react">Back</a>
            <button id="pdf-button" onClick={printDiv}>{props?"Download my CV":" CV- "}</button>
            <div className="relevance">Relevance: 
            <button id={relevance==='all' ? 'selected':'notSelected'} name="all" className="relevance-button" onClick={changeRelevanceFilter}>{props?"All":""}</button>
            <button id={relevance==='coding' ? 'selected':'notSelected'} name="coding" onClick={changeRelevanceFilter} className="relevance-button">{props?"Coding/IT stack":""}</button>
            <button id={relevance==='shopAssistant' ? 'selected':'notSelected'} name="shopAssistant" onClick={changeRelevanceFilter} className="relevance-button">{props?"Client interactions":"  "}</button>
            </div>
            <div id="portfolio">
                <table className="scrollable" id="example">
                    <tbody className="scrollable" >
                        <tr id="information-row"><td colSpan="2" id="table-indentifier">{props?"Personal Information":" "}</td><td className="zero-width"></td></tr>
                        
                        {personalData}
                        <tr><td id="table-indentifier" colSpan="2">{props?"Professional experience":" "}</td><td className="zero-width"></td></tr>
                        {workExperience}
                        <tr><td id="table-indentifier" colSpan="2">{props?"Special courses, qualifications and education":" ,   "}</td><td className="zero-width"></td></tr>
                        {education}
                        <tr><td id="table-indentifier" colSpan="2">{props?"Languages":""}</td><td className="zero-width"></td></tr>
                        {languages}
                        <tr><td id="table-indentifier" colSpan="2">{props?"Personal skills and competences":"   "}</td><td className="zero-width"></td></tr>
                        {socialSkills}
                        {organizationalSkills}
                        {techSkills}
                        {artSkills}
                        {otherSkills}
                        {otherInfo}
                        <tr><td id="table-indentifier" colSpan="2">{props?"Certificates and other documents":"   "}</td><td className="zero-width"></td></tr>
                        {certificates}
                    </tbody>
                </table>
                
            </div>
            <div className="scrollable" id="logos">
                <div className="exp-logo"><img className="photoshop-logo" id="exp-logo" src="https://seeklogo.com/images/A/adobe-photoshop-logo-7B88D7B5AA-seeklogo.com.png" alt="Photoshop"/></div>
                <div className="exp-logo"><img className="blender-logo" id="exp-logo" src="https://upload.wikimedia.org/wikipedia/commons/thumb/0/0c/Blender_logo_no_text.svg/2503px-Blender_logo_no_text.svg.png" alt="Blender"/></div>
                <div className="exp-logo"><img className="csp-logo" id="exp-logo" src="https://images-wixmp-ed30a86b8c4ca887773594c2.wixmp.com/i/a7ab2c08-5091-4c8e-a708-9bfa3c9639bf/dbr01gl-46b8dbef-6184-4b5f-97f4-439800abd0b8.png" alt="Csp"/></div>
                <div className="exp-logo"><img className="js-logo" id="exp-logo" src="https://upload.wikimedia.org/wikipedia/commons/thumb/9/99/Unofficial_JavaScript_logo_2.svg/2048px-Unofficial_JavaScript_logo_2.svg.png" alt="Javascript"/></div>
                <div className="exp-logo"><img className="react-logo" id="exp-logo" src="https://icon-library.com/images/react-icon/react-icon-15.jpg" alt="react"/></div>
                <div className="exp-logo"><img className="qb64-logo" id="exp-logo" src="data:image/jpeg;base64," alt="react"/></div>
                <div className="exp-logo"><img className="arduino-logo" id="exp-logo" src="data:image/png;base64," alt="react"/></div>
                <div className="exp-logo"><img className="react-logo" id="exp-logo" src="https://allpcworld.com/wp-content/uploads/2020/12/Download-MediBang-Paint-Pro-26.jpg" alt="react"/></div>
            </div>

    </div>

}

export default CV;